.notifyWrapper {
    @apply fixed top-28 left-5 md:left-16 flex flex-row overflow-hidden flex-nowrap rounded justify-between items-center px-3 py-1 bg-eco-success duration-[500ms] ease-in-out;
}
.notifyShow {
    @apply fixed top-28 w-11/12 md:w-10/12 lg:w-11/12 translate-x-0 visible opacity-100;
}
.notifyHide {
    @apply fixed top-28 opacity-0 -translate-x-96 invisible w-10;
}

.notifyText {
    @apply font-semibold font-acumin-pro text-eco-dark tracking-wide md:whitespace-nowrap;
}

.notifyIcon {
    @apply text-eco-dark text-xl cursor-pointer;
}