
.contentFocus {}

.contentFocusContent {
    @apply text-eco-white text-left text-6xl leading-none font-acumin-pro font-bold;
    @apply md:text-[82px];
}

.contentAboutWrapper {
    @apply grid grid-cols-1 mt-20;
    @apply md:grid-cols-3 md:gap-4;
}

.aboutWrapper {
    @apply flex flex-col col-span-2 pr-0 text-eco-white;
    @apply md:pr-20;
    @apply lg:pr-52;
}

.aboutProblem {
    @apply mb-10 font-acumin-pro font-medium text-xl;
}

.aboutHelp {
    @apply font-acumin-pro text-xl pr-0 font-bold my-6;
    @apply md:pr-20;
    @apply lg:pr-52;
}

.signUpWrapper {
    /* @apply flex flex-col pr-32 items-start; */
}

.aboutGimmick {
    @apply pr-0 text-eco-grey text-sm font-acumin-pro mt-10;
    @apply md:mt-0;
    @apply lg:pr-10 lg:mt-0;
}

