.sectionTitle {
    @apply text-5xl font-acumin-pro font-bold mb-2;
}

.projectWrapper {
    @apply grid gap-7 mt-8;
    @apply sm:grid-cols-2 sm:gap-7;
    @apply md:grid-cols-3 md:gap-6;
    @apply lg:grid-cols-4 lg:gap-4;
}

.projectCard {
    @apply cursor-pointer py-3 border-t my-4 border-t-eco-grey;
}

.projectDirectory {
    @apply text-eco-grey uppercase font-medium tracking-wide text-[11px] mb-2;
}

.projectCardTitle {
    @apply tracking-wide font-acumin-pro text-eco-white text-2xl;
}

.projectCard:hover .projectCardTitle {
    @apply underline; 
}

.theBadge {
    @apply bg-eco-purple pl-2 w-36 h-28 flex flex-col justify-start;
}

.badgeIcon {
    @apply text-7xl text-eco-white;
}

.badgeText {
    @apply text-xs text-eco-white pl-6;
}

.badgeText:hover {
    @apply underline; 
}

.allBadges{
    @apply flex flex-row space-x-1;
}


/* Stats styles */

.statsLink {
    @apply underline text-lg text-eco-green;    
}

.statsWebWrapper {
    @apply flex flex-row items-center justify-start;
}

.statsEditButton {
    @apply ml-2 text-xs text-eco-white px-3 pt-1 bg-eco-purple rounded-3xl mb-2;
}

.statsTitle {
    @apply text-6xl font-acumin-pro font-bold mb-2;
}

.statsSubTitle {
    @apply font-acumin-pro text-2xl my-1 font-medium mt-6;
}

.statsDateWrapper {
    @apply flex flex-row justify-between items-center w-48;
}

.statsDate {
    @apply font-acumin-pro text-sm;
}

.statsValueWrapper {
    @apply grid gap-5 mt-5 mb-16;
    @apply md:grid-cols-3 md:gap-4;
}

.statsWrapper {
    @apply border-y border-y-eco-white my-2 py-4 pb-8;
}

.statsDataWrapper {
    @apply w-fit my-5;
}

.statsSubWrapper {
    @apply flex w-full md:w-fit flex-row flex-wrap md:flex-nowrap justify-between items-center;
}

.statsContent {
    @apply mr-4 mb-3 sm:mr-6 md:mr-8;
}

.dataTitle {
    @apply font-acumin-pro text-[11px] uppercase text-left my-1;
}

.dataValue {
    @apply font-acumin-pro text-3xl font-medium text-left;
}

.statsInsights {
    @apply px-4 pt-2 pb-1 rounded-3xl border-2 border-eco-green bg-eco-dark text-eco-green;
}

.valueTitle {
    @apply font-semibold font-acumin-pro text-base text-left
}

.valueWrapper {
    @apply  relative w-full h-28 border border-eco-purple rounded my-1 bg-[#041433];
}

.valueStatsWrapper {
    @apply flex flex-row justify-start items-center mt-3;
}

.valueStats {
    @apply mr-10;
}

.valueButton {
    @apply text-xs text-eco-green border border-eco-green bg-eco-dark px-2.5 pt-1 rounded-3xl my-1;
}

.valueBadge {
    @apply absolute top-3 left-3 text-eco-grey font-semibold uppercase text-xs;
}
/*  Set-Up CSS Section */

.titleWrapper{
    @apply text-3xl;
}

.subTitleWrapper{
    @apply pt-5 font-acumin-pro font-bold tracking-wide tracking-wider;
}

.intructionwrapper{
    @apply pt-2 pb-2;

}

.codeWrapper{
    @apply bg-eco-black text-eco-green mr-4 mb-4 pl-2 pr-6 py-2 tracking-wide tracking-wider font-acumin-pro ;
}

/* End Of Set-Up CSS Section */

/* Create New Project CSS */

.pageHead{
    @apply text-4xl tracking-wide tracking-wide font-acumin-pro font-medium pb-10;
}

.pageSubHead{
    @apply text-very-small font-acumin-pro tracking-wide tracking-widest;
}

.pageSubHead2{
    @apply font-medium font-acumin-pro tracking-wide tracking-widest pb-8;
}

.input{
    @apply py-9;
}

/* End of New Project CSS */
