.sectionTitle {
    @apply text-5xl font-acumin-pro font-bold mb-2;
}

.orgWrapper {
    @apply grid gap-7 mt-8;
    @apply sm:grid-cols-2 sm:gap-7;
    @apply md:grid-cols-3 md:gap-6;
    @apply lg:grid-cols-4 lg:gap-4;
}

.orgCard {
    @apply cursor-pointer py-3 border-t-2 my-4 border-t-eco-grey;
}

.orgCardTitle {
    @apply font-bold cursor-pointer tracking-wide font-acumin-pro text-eco-white text-lg hover:underline;
}

.orgWrapper:hover .orgCardTitle {
    @apply underline;
}
