.formWrapper {
    @apply grid grid-cols-1 items-start justify-between;
    @apply md:grid-cols-1;
    @apply lg:grid-cols-2 ;
}

.sectionTitle {
    @apply lg:col-span-2 text-eco-white font-acumin-pro text-4xl font-bold mb-5 pr-8;
}

/* .input{
    @apply pr-0 ;
} */

.subSectionWrapper{
    @apply flex flex-col;
}

.sectionSubTitle {
    @apply text-eco-white font-acumin-pro text-lg font-bold mb-2;
}

.sectionInfo {
    @apply text-eco-white font-acumin-pro text-sm mb-5;
}

.sectionLink {
    @apply text-sm my-5 font-acumin-pro w-fit text-eco-white border-b border-b-transparent hover:border-b-eco-white duration-300 ease-in-out;
}

.needWrapper{
    @apply pl-0;
    @apply md:pl-8;
    @apply lg:pl-28;
    /* @apply lg: pl-28; */
}