.sectionTitle {
    @apply text-5xl font-acumin-pro font-bold mb-2;
}

.sectionSubTitle {
    @apply font-acumin-pro text-base my-1 font-semibold mt-6;
}

.sectionDateWrapper {
    @apply flex flex-row justify-between items-center w-48;
}

.sectionDate {
    @apply font-acumin-pro text-sm;
}

.sectionDataWrapper {
    @apply w-fit mt-10;
}

.sectionSubWrapper {
    @apply flex w-full md:w-fit flex-row flex-wrap md:flex-nowrap justify-between items-center;
}

.sectionContent {
    @apply mr-4 mb-3 sm:mr-6 md:mr-8;
}

.dataTitle {
    @apply font-acumin-pro text-sm uppercase text-left my-1;
}

.dataValue {
    @apply font-acumin-pro text-3xl font-medium text-left;
}

.dataScore {
    @apply font-acumin-pro text-lg text-eco-green mx-auto;
}

.compensationWrapper {
    @apply border-t border-t-eco-grey mt-10 py-2
}
