/* Top bar */

.topWrapper {
    @apply relative flex flex-row justify-between items-end px-8 py-8 bg-eco-dark;
    @apply md:px-16;
    @apply lg:px-8;
}

.logoWrapper {
    @apply flex flex-row items-end;
}

.logoIcon {
    @apply h-8 w-auto;
}

.logoText {
    @apply font-acumin-pro -mb-1.5 text-sm ml-2 text-eco-white;
}

.topNavWrapper {
    @apply flex flex-col items-end text-eco-white;
}

.topNavUser {
    /* @apply; */
    @apply font-acumin-pro uppercase text-[12px] font-medium duration-300 ease-in-out;
}

.topNavUserLink {
    @apply cursor-pointer hover:underline;
}

.topNavLinks {
    @apply flex flex-row absolute -bottom-3 w-full left-2 flex-wrap;
    @apply sm:relative sm:w-fit;
}

.topNavLink {
    @apply ml-4 hover:underline duration-300 ease-in-out;
}

.topNavLinkText {
    @apply font-acumin-pro font-medium text-sm tracking-wider;
}