.contentWrapper {
    @apply px-8 py-10 w-full;
    @apply md:px-28 md:py-10;
    @apply lg:px-52 lg:py-10;
}

.versionText {
    @apply text-eco-green text-sm text-center font-bold font-acumin-pro tracking-wide;
}

.contentTitle {
    @apply text-eco-white font-acumin-pro font-bold text-center text-6xl my-3;
}

.contentAbout {
    @apply text-center font-acumin-pro px-8 text-eco-white text-opacity-70 text-base;
    @apply md:px-28 md:text-lg;
    @apply lg:px-52 lg:text-xl;

}

.contentReasonWrapper {
    @apply grid grid-cols-1 gap-2 mt-16 mb-10;
    @apply md:grid-cols-2 md:gap-2;
}

.contentReasonTitle {
    @apply text-eco-white text-base font-bold font-acumin-pro mb-2;
    @apply md:mb-5;
}

.contentReasonText {
    @apply text-eco-white font-acumin-pro text-opacity-70 text-left text-base;
    @apply md:px-10;
}

/* Yolo calculator */

.calculatorWrapper {
    @apply bg-eco-purple px-8 my-5 py-10;
    @apply md:px-16
}

.sectionWrapper {
    @apply flex flex-col justify-between items-start my-2 p-5 px-0 border-b border-b-eco-white;
    @apply lg:flex-row lg:items-center lg:px-5;
}

.sectionSubWrapper {
    @apply w-full grid grid-cols-1 gap-7;
    @apply lg:grid-cols-4 lg:gap-5
}

.sectionTitle {
    @apply mr-28 w-48 font-bold text-2xl text-eco-white font-acumin-pro mb-3;
    @apply lg:mb-0;
}

.sectionLabel {
    @apply text-xs text-eco-white text-opacity-90 font-bold font-acumin-pro;
}

.sectionField {
    @apply bg-eco-white bg-opacity-40 outline-none rounded-md px-2 py-2 text-sm text-eco-white w-40 my-1;
}

.sectionSubTitle {
    @apply text-sm text-eco-white font-bold font-acumin-pro;
}

.sectionValue {
    @apply text-lg text-eco-white font-bold font-acumin-pro;
}

.sectionButton {
    @apply bg-eco-white my-2 px-16 w-full py-1.5 rounded-3xl shadow-md text-eco-dark text-lg;
    @apply lg:w-fit;
}

.aboutHelp {
    @apply font-acumin-pro text-eco-white text-xl pr-52 font-bold my-6;
}

